* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
}

.clocks {
  color: #00f;
  font-size: 18px;
  font-weight: 500;
}

body {
  min-height: 100vh;
  background-color: #091921;
  justify-content: center;
  align-items: center;
  display: flex;
}

.clock {
  width: 350px;
  height: 350px;
  background: url("clock.7b329d1a.png") 0 0 / cover;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 0 15px #86868659;
}

.clock:before {
  content: "";
  width: 15px;
  height: 15px;
  z-index: 999;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
}

.clock .hour, .clock .min, .clock .sec {
  position: absolute;
}

.clock .hour, .hr {
  width: 160px;
  height: 160px;
}

.clock .min, .mn {
  width: 190px;
  height: 190px;
}

.clock .sec, .sc {
  width: 230px;
  height: 230px;
}

.hr, .mn, .sc {
  border-radius: 50%;
  justify-content: center;
  display: flex;
  position: absolute;
}

.hr:before {
  content: "";
  width: 8px;
  height: 80px;
  z-index: 10;
  background-color: #ff0;
  border-radius: 10px 10px 0 0;
}

.mn:before {
  content: "";
  width: 4px;
  height: 100px;
  z-index: 11;
  background-color: #fff;
  border-radius: 10px 10px 0 0;
}

.sc:before {
  content: "";
  width: 2px;
  height: 140px;
  z-index: 12;
  background-color: red;
  border-radius: 10px 10px 0 0;
}

